import React, { useState, useEffect } from 'react';
import { NavLink } from 'react-router-dom';
import styles from './Header.module.css';
import logo from './img/clanlogo.webp';

export default function Header() {
  const [isSidebarVisible, setIsSidebarVisible] = useState(false);
  const [lastScrollY, setLastScrollY] = useState(0);
  const [headerClass, setHeaderClass] = useState(styles.header);
  const [scrollTimeout, setScrollTimeout] = useState(null);

  const toggleSidebar = () => {
    setIsSidebarVisible(!isSidebarVisible);
  };

  useEffect(() => {
    const handleScroll = () => {
      const currentScrollY = window.scrollY;
      if (scrollTimeout) {
        clearTimeout(scrollTimeout);
      }
      setScrollTimeout(
        setTimeout(() => {
          if (Math.abs(currentScrollY - lastScrollY) >= 80) {
            if (currentScrollY < lastScrollY) {
              setHeaderClass(`${styles.header} ${styles.headerVisible}`);
            } else {
              setHeaderClass(`${styles.header} ${styles.headerHidden}`);
            }
            setLastScrollY(currentScrollY);
          }
        }, 100),
      );
    };

    window.addEventListener('scroll', handleScroll);

    return () => {
      window.removeEventListener('scroll', handleScroll);
      if (scrollTimeout) {
        clearTimeout(scrollTimeout);
      }
    };
  }, [lastScrollY, scrollTimeout]);

  return (
    <>
      <header className={headerClass}>
        <div className={styles.navSection}>
          <NavLink
            to='/home'
            className={styles.navLink}
            onClick={toggleSidebar}
          >
            HOME
          </NavLink>
          <NavLink
            to='/home#about-us'
            className={styles.navLink}
            onClick={toggleSidebar}
          >
            ABOUT US
          </NavLink>
          <NavLink
            to='/home#join-us'
            className={styles.navLink}
            onClick={toggleSidebar}
          >
            JOIN US
          </NavLink>
        </div>
        <div className={styles.logoSection}>
          <NavLink to='/home' onClick={toggleSidebar}>
            <img src={logo} className={styles.logo} alt='logo' />
          </NavLink>
          <NavLink
            to='/home'
            onClick={toggleSidebar}
            className={styles.logoText}
          >
            <h1>SONS OF ANARCHY</h1>
          </NavLink>
        </div>

        <div className={styles.navSection}>
          <NavLink
            to='/guides'
            className={styles.navLink}
            onClick={toggleSidebar}
          >
            GUIDES
          </NavLink>
          <NavLink
            to='/maps'
            className={styles.navLink}
            onClick={toggleSidebar}
          >
            MAPS
          </NavLink>
          <NavLink
            to='/gallery'
            className={styles.navLink}
            onClick={toggleSidebar}
          >
            GALLERY
          </NavLink>
        </div>

        <button className={styles.navToggle} onClick={toggleSidebar}>
          ☰
        </button>
      </header>
      <div
        className={`${styles.sidebarPhone} ${isSidebarVisible ? styles.show : ''}`}
      >
        <NavLink to='/home' className={styles.navLink} onClick={toggleSidebar}>
          HOME
        </NavLink>
        <NavLink
          to='/home#about-us'
          className={styles.navLink}
          onClick={toggleSidebar}
        >
          ABOUT US
        </NavLink>
        <NavLink
          to='/home#join-us'
          className={styles.navLink}
          onClick={toggleSidebar}
        >
          JOIN US
        </NavLink>
        <NavLink
          to='/guides'
          className={styles.navLink}
          onClick={toggleSidebar}
        >
          GUIDES
        </NavLink>
        <NavLink to='/maps' className={styles.navLink} onClick={toggleSidebar}>
          MAPS
        </NavLink>
        <NavLink
          to='/gallery'
          className={styles.navLink}
          onClick={toggleSidebar}
        >
          GALLERY
        </NavLink>
      </div>
    </>
  );
}
