import React, { useEffect, useRef, lazy, Suspense } from 'react';
import { useLocation } from 'react-router-dom';
import { Layout } from '../../components/Layout';
// import { RunningLine } from '../../components/RunningLine';
// import { CarouselCustomMain } from '../../components/CarouselCustomMain';
// import { AboutUs } from '../../components/AboutUs';
// import { ApplyForm } from '../../components/ApplyForm';
import useIntersectionObserver from '../../hooks/useIntersectionObserver';
// import styles from './HomePage.module.css';

import guidesImage from './img/guides.webp';
import joinusImage from './img/joinus.webp';
import galleryImage from './img/gallery.webp';
import mapsImage from './img/maps.webp';
import homeimage from './img/homeimage.webp';

const CarouselCustomMain = lazy(
  () => import('../../components/CarouselCustomMain/CarouselCustomMain'),
);
const AboutUs = lazy(() => import('../../components/AboutUs/AboutUs'));
const RunningLine = lazy(
  () => import('../../components/RunningLine/RunningLine'),
);
const ApplyForm = lazy(() => import('../../components/ApplyForm/ApplyForm'));

const carouselItems = [
  { src: guidesImage, caption: 'Read our up to date guides', link: '/guides' },
  {
    src: joinusImage,
    caption: 'Become a part of us',
    action: 'scroll',
    target: '#join-us',
  },
  { src: galleryImage, caption: 'Watch our latest content', link: '/gallery' },
  { src: mapsImage, caption: 'Use our useful maps', link: '/maps' },
];

export default function HomePage({ toastRef }) {
  const location = useLocation();
  const refCarousel = useRef(null);
  const refAboutUs = useRef(null);
  const refApplyForm = useRef(null);
  const refText = useRef(null);
  const refLastParagraph = useRef(null);

  const reveal = (element, observer, index) => {
    element.style.animationDelay = `${index * 0.2}s`; // Stagger the animations
    element.classList.remove('hidden');
    element.classList.add('fade-in-up');
  };

  useIntersectionObserver(refCarousel, { threshold: 0.1 }, reveal, 0);
  useIntersectionObserver(refText, { threshold: 0.1 }, reveal, 1);
  useIntersectionObserver(refAboutUs, { threshold: 0.1 }, reveal, 2);
  useIntersectionObserver(refApplyForm, { threshold: 0.1 }, reveal, 3);
  useIntersectionObserver(refLastParagraph, { threshold: 0.1 }, reveal, 4);

  // Scroll to the #join-us section if the URL hash is #joinus
  useEffect(() => {
    if (location.hash === '#join-us') {
      setTimeout(() => {
        if (refApplyForm.current) {
          refApplyForm.current.scrollIntoView({ behavior: 'smooth' });
        }
      }, 100);
    } else if (location.hash === '#about-us') {
      setTimeout(() => {
        if (refAboutUs.current) {
          refAboutUs.current.scrollIntoView({ behavior: 'smooth' });
        }
      }, 100);
    }
  }, [location]);

  return (
    <Layout>
      <Suspense fallback={<div>Loading image...</div>}>
        <img src={homeimage} className='main-image' alt='Main' />{' '}
      </Suspense>
      <Suspense fallback={<div>Loading Running line...</div>}>
        <RunningLine />{' '}
      </Suspense>

      <div ref={refCarousel} className='hidden'>
        <Suspense fallback={<div>Loading Carousel...</div>}>
          <CarouselCustomMain carouselItems={carouselItems} />
        </Suspense>
      </div>

      <div className='container hidden' ref={refText}>
        <h3 className='text'>
          Experience a community that <span className='accent'>works</span>{' '}
          together,
          <span className='accent'> grows</span> together, and enjoys
          <span className='accent'> the game</span> together!
        </h3>
      </div>

      <div id='about-us' ref={refAboutUs} className='hidden'>
        <Suspense fallback={<div>Loading About us...</div>}>
          <AboutUs />
        </Suspense>
      </div>
      <div id='join-us' ref={refApplyForm} className='hidden'>
        <Suspense fallback={<div>Loading Apply Form...</div>}>
          <ApplyForm toastRef={toastRef} />
        </Suspense>
      </div>

      <div className='container'>
        <p
          className='text hidden'
          style={{ margin: '80px 0px' }}
          ref={refLastParagraph}
        >
          We are looking for individuals who share our values, who can bring
          their own unique skills and personality to our community. Whether
          you’re an experienced gamer or a newcomer, as long as you have a love
          for the game and a will to cooperate, you’ll find a home with us.
        </p>
      </div>
    </Layout>
  );
}
