// Importing necessary libraries and components
import './App.css';
import { Toast } from 'primereact/toast';
import { useRef } from 'react';
import 'primereact/resources/primereact.min.css';

import { HashRouter, Routes, Route } from 'react-router-dom';
import { Navigate } from 'react-router-dom';

// Import your page components
import HomePage from './pages/HomePage/HomePage';
import GuidesPage from './pages/GuidesPage/GuidesPage';
import PrivacyPolicy from './pages/PrivacyPolicy/PrivacyPolicy';
import CombatFundamentals from './pages/Guides/CombatFundamentals/CombatFundamentals'; // Import the new component
import ClanWarsGuide from './pages/Guides/ClanWars/ClanWars';

function App() {
  const toast = useRef(null);

  return (
    <HashRouter>
      <Toast ref={toast} position='bottom-left' style={{ position: 'fixed' }} />

      <Routes>
        <Route path='/' element={<Navigate replace to='/home' />} />
        <Route path='/home' element={<HomePage toastRef={toast} />} />
        <Route path='/guides' element={<GuidesPage />} />
        <Route
          path='/guides/combat-fundamentals-guide'
          element={
            <CombatFundamentals
              guideContent={{
                title: 'Combat Fundamentals',
                subtitle: "From Peeker's Advantage to Crosshair Precision",
              }}
            />
          }
        />
        <Route
          path='/guides/clan-wars-guide'
          element={
            <ClanWarsGuide
              guideContent={{
                title: 'Clan wars',
                subtitle: 'Everything you need to know about clan wars',
              }}
            />
          }
        />
        {/* New Route */}
        <Route path='/privacy-policy' element={<PrivacyPolicy />} />
      </Routes>
    </HashRouter>
  );
}

export default App;
