import { Header } from '../Header';
import { Footer } from '../Footer';
import styles from './LayoutGuide.module.css';
import { useNavigate } from 'react-router-dom';
import { Suspense } from 'react';
import guidesimage from './img/guidesimage.webp';
// import { useRef } from 'react';
// import useIntersectionObserver from '../../hooks/useIntersectionObserver';

function LayoutGuide({ guideContent, children }) {
  const navigate = useNavigate();
  // const refTitle = useRef(null);
  // const refSubtitle = useRef(null);

  // const reveal = (element, observer, index) => {
  //   element.style.animationDelay = `${index * 0.2}s`; // Stagger the animations
  //   element.classList.remove('hidden');
  //   element.classList.add('fade-in-up');
  // };
  // useIntersectionObserver(refTitle, { threshold: 0.1 }, reveal, 1);
  // useIntersectionObserver(refSubtitle, { threshold: 0.1 }, reveal, 1);
  return (
    <>
      <Header />

      <main>
        {' '}
        <div className='container'>
          <button
            className={styles.backButton}
            onClick={() => navigate('/guides')}
          >
            ← Back to Guides
          </button>
        </div>
        <Suspense fallback={<div>Loading image...</div>}>
          <img
            src={guidesimage}
            className='main-image'
            alt='Guide Background'
            style={{ position: 'absolute' }}
          />
        </Suspense>
        <div className={styles.space} />
        <div className='container'>
          <div className={`${styles.titleSection} `}>
            <h1 className={styles.headingGuide}>{guideContent.title}</h1>
            <hr className={styles.hr} />
            <p className={styles.subtitle}>{guideContent.subtitle} </p>
          </div>
        </div>
        <div className={styles.textFormatting}>{children} </div>
        <div
          className='container'
          style={{ marginTop: '40px', marginBottom: '80px' }}
        >
          <button
            className={styles.backButtonBottom}
            onClick={() => navigate('/guides')}
          >
            ← Back to Guides
          </button>
        </div>
      </main>
      <Footer />
    </>
  );
}

export default LayoutGuide;
