import { useRef, lazy, Suspense } from 'react';
import { Layout } from '../../components/Layout';
import styles from './GuidesPage.module.css';
import guidesimage from './img/guidesimage.webp';
// import TextGuides from '../../components/TextGuides/TextGuides';
// import StatisticsAndSpreadsheets from '../../components/StatisticsAndSpreadsheets/StatisticsAndSpreadsheets';
// import { VideoGuides } from '../../components/VideoGuides';
import useIntersectionObserver from '../../hooks/useIntersectionObserver';
const TextGuides = lazy(() => import('../../components/TextGuides/TextGuides'));
const StatisticsAndSpreadsheets = lazy(
  () =>
    import(
      '../../components/StatisticsAndSpreadsheets/StatisticsAndSpreadsheets'
    ),
);
const VideoGuides = lazy(
  () => import('../../components/VideoGuides/VideoGuides'),
);

export default function GuidesPage() {
  const refTextOverImage = useRef(null);
  const refTextGuides = useRef(null);
  const refVideoGuides = useRef(null);
  const refStatistics = useRef(null);

  const reveal = (element, observer, index) => {
    element.style.animationDelay = `${index * 0.2}s`; // Stagger the animations
    element.classList.remove('hidden');
    element.classList.add('fade-in-up');
  };

  useIntersectionObserver(refTextOverImage, { threshold: 0.1 }, reveal, 2);
  useIntersectionObserver(refTextGuides, { threshold: 0.1 }, reveal, 1);
  useIntersectionObserver(refVideoGuides, { threshold: 0.1 }, reveal, 0);
  useIntersectionObserver(refStatistics, { threshold: 0.1 }, reveal, 0);

  return (
    <Layout>
      <Suspense fallback={<div>Loading image...</div>}>
        <img
          src={guidesimage}
          className='main-image'
          alt='Main'
          style={{ position: 'absolute' }}
        />
      </Suspense>
      <div className={styles.space} />
      <div className='container'>
        <div
          className={`${styles.textOverImage} hidden`}
          ref={refTextOverImage}
        >
          <h2 className={styles.headingGuides}>SELECT A GUIDE TO READ</h2>
          <hr className={styles.hr} />
        </div>
        <div ref={refTextGuides} className='hidden'>
          <Suspense fallback={<div>Loading Text Guides...</div>}>
            <TextGuides />{' '}
          </Suspense>
        </div>
        <div className={styles.gridContainer}>
          <div
            className={`${styles.gridItemLarge} hidden`}
            ref={refVideoGuides}
          >
            <Suspense fallback={<div>Loading Video Guides...</div>}>
              <VideoGuides />
            </Suspense>
          </div>
          <div className={`${styles.gridItemSmall} hidden`} ref={refStatistics}>
            <Suspense fallback={<div>Loading Spreadsheets Guides...</div>}>
              <StatisticsAndSpreadsheets />
            </Suspense>
          </div>
        </div>
      </div>
    </Layout>
  );
}
