import { Suspense } from 'react';
// import { useRef, Suspense } from 'react';
// import { useRef } from 'react';
import { LayoutGuide } from '../../../components/LayoutGuide';
import styles from './ClanWars.module.css';
// import useIntersectionObserver from '../../../hooks/useIntersectionObserver';
import TradePackImage from './img/cw-trade-pack.webp';
import YourStuffImage from './img/cw-your-own-stuff.webp';
import quickAccessWheel from './img/quick-access-wheel.webp';
import brine from './img/brine.webp';
import vodka from './img/vodka.webp';
import lemongrass from './img/lemongrass.webp';

export default function ClanWars({ guideContent }) {
  // const refTitle = useRef(null);
  // const refContent = useRef(null);
  // const refRelated = useRef(null);

  // const reveal = (element, observer, index) => {
  //   element.style.animationDelay = `${index * 0.2}s`; // Stagger the animations
  //   element.classList.remove('hidden');
  //   element.classList.add('fade-in-up');
  // };

  // Lazy load images
  // useIntersectionObserver(refTitle, { threshold: 0.1 }, reveal, 1);
  // useIntersectionObserver(refContent, { threshold: 0.1 }, reveal, 0);
  // useIntersectionObserver(refRelated, { threshold: 0.1 }, reveal, 0);

  return (
    <LayoutGuide guideContent={guideContent}>
      <div className='container'>
        <div className={`${styles.contentSection}`}>
          <h2>How an average clan wars buy pack should look like</h2>
          <p>
            Successful clan wars begin with a <b>solid trade pack</b>. If you
            don’t have a <b>decent trade pack</b> - you won’t reach your peak
            efficiency.
          </p>
          <Suspense fallback={<div>Loading image...</div>}>
            <img
              src={TradePackImage}
              alt='Clan War Strategy Visualization'
              className={styles.image}
            />
          </Suspense>
          <ol>
            <li>
              <b>Clarinol</b> for health or <b>Desperol</b> for speed depending
              on your role
            </li>
            <li>
              <b>Hemostat</b> for extra healing and less bleeding (there is a
              lot of snipers at clanwars).
            </li>
            <li>
              <b>Energy drink</b> for movement speed
            </li>
            <li>
              <b>Arnie tonic</b> for extra stamina and carry weight if you bring
              some heavy stuff with you
            </li>
            <li>
              <b>Blue medkits</b> are your main medkits
            </li>
            <li>
              <b>Yellow medkits</b> to counter poison effects from Clarinol /
              Desperol / Vodka / Hemostat
            </li>
            <li>
              <b>Bandages</b> against snipers
            </li>
            <li>
              Level 4 (IV) <b>armour plates</b> are the best for the value
            </li>
            <li>
              <b>Incendiary ammo</b> is great for clan wars
            </li>
            <li>
              Take some <b>AP ammo</b> too to save clan treasury and your own
              money
            </li>
            <li>
              In clanwars you can buy <b>sniper ammo</b> with 27.5% armour
              penetration
            </li>
            <li>
              Take some <b>cheap ammo</b> for your sniper in case you run out of
              sniper ammo
            </li>
            <li>
              <b>Secondary weapon ammo</b>
            </li>
          </ol>
          <br />
          <h4>
            However, some stuff you have to bring on your own. Not everything
            can be bought for clan treasury.
          </h4>
          <Suspense fallback={<div>Loading image...</div>}>
            <img
              src={YourStuffImage}
              alt='Clan War Strategy Visualization'
              className={styles.image}
            />
          </Suspense>
          <ol>
            <li>
              Any <b>grenades crate</b> - if you see your team is about to camp
              enemy tent - you unpack some grenades and start throwing them at
              the tent, while your teammates cover you.
            </li>
            <li>
              <b>SBP ammo</b> for main gun or sniper rifle - for important
              moments, e.g., you are alone solo defending a far point and want
              to have a better chance against multiple enemies.
            </li>
            <li>
              <b>Crooked claw vodka</b> - you get a lot of it from tactical
              reserve. Extra vitality and healing. Effects don’t wear off after
              death.
            </li>
            <li>
              <b>Injectors</b> - you can buy them for clan money, but you can
              also buy them for rubles, to not risk running out of limit.
            </li>
            <li>
              <b>Green medkits</b> - for important situations or if you
              accidentally stepped into bio 3 or rad 4 area.
            </li>
            <li>
              <b>Combat peas</b> - A solid choice for food slot.
            </li>
          </ol>
          <h4>
            And now as we have the right tools, let’s optimize usage of them
          </h4>
          <p>(Hold C - medkit button to open it)</p>
          <i>
            That’s just an example of a quick access wheel, you need to adjust
            it according to your needs.
          </i>
          <Suspense fallback={<div>Loading image...</div>}>
            <img
              src={quickAccessWheel}
              alt='Clan War Strategy Visualization'
              className={styles.image}
            />
          </Suspense>

          <ol>
            <li>Main gun</li>
            <li>
              Shotgun (you don’t need it for clan wars, that’s for open world
              lol)
            </li>
            <li>Sniper rifle</li>
            <li>SBP ammo or other powerful ammo</li>
            <li>Consumables for quick use</li>
            <li>Consumables for quick use</li>
            <li>Consumables for quick use</li>
            <li>Consumables for quick use</li>
            <li>Bandages against snipers</li>
          </ol>

          <h2>Clan War Communication Guidelines</h2>

          <p>
            Effective communication is crucial in Clan Wars to ensure that
            important information is conveyed quickly and clearly. Below are the
            guidelines and rules you must follow to maintain effective
            communication during intense situations.
          </p>

          <h3>General Rules for Clan War Participation</h3>
          <ul>
            <li>
              <b>Join the Clan Wars Voice Channel</b>: Everyone must be present
              in the designated voice channel on Discord.
            </li>
            <li>
              <b>Communication Settings</b>:
              <ul>
                <li>
                  Use <b>Push to Talk</b> or <b>Push to Mute</b> to avoid
                  unnecessary noise.
                </li>
                <li>
                  <b>No soundboards</b> or other disruptive audio tools.
                </li>
                <li>
                  <b>Only English</b> is allowed for communication.
                </li>
              </ul>
            </li>
            <li>
              <b>Be Punctual</b>: Arrive <b>15 minutes before</b> the start of
              the event. If you cannot make it, inform the team in the{' '}
              <i>❌┃leave</i> channel.
            </li>
            <li>
              <b>Respect Your Teammates</b>: No disruptive behavior or yapping.
              Keep conversations concise and focused.
            </li>
          </ul>

          <h3>Callout Guidelines</h3>
          <p>
            <b>Callouts</b> are brief and precise messages that convey critical
            information about the enemy. Here’s how to structure your callouts:
          </p>
          <ul>
            <li>
              <b>Standard Callout Format</b>: ENEMY NUMBER + CLASS + LOCATION.
              Example: <blockquote>“Three centurions roof”</blockquote>
              <blockquote>“Two SVDs north”</blockquote>
            </li>
            <li>
              <b>Include the following Information</b>:
              <ul>
                <li>
                  <b>How many enemies</b> are there?
                </li>
                <li>
                  <b>What armor</b> is the enemy using?
                </li>
                <li>
                  <b>Where</b> are they located?
                </li>
              </ul>
            </li>
            <li>
              <b>Efficiency is Key</b>: Use only the necessary words to convey
              the message. Avoid full sentences unless absolutely needed.
              Example of a bad callout:
              <blockquote>
                <i>
                  “What the fuck did you just fucking say about me? I’ll have
                  you know I graduated top of my class in the XM1014 masters...”
                </i>
              </blockquote>
            </li>
            <li>
              <b>When to Call Out</b>: Call out only when the information is{' '}
              <b>important</b> to the team. No need to report single enemies
              that are already covered or aren’t a threat. If multiple enemies
              are approaching, use terms like <b>“big push”</b> or{' '}
              <b>“multiple hostiles”</b>.
            </li>
          </ul>

          <h3>Communication Hierarchy</h3>
          <p>
            During a clan war, the order of communication priority is as
            follows:
          </p>
          <ol>
            <li>
              <b>Tactic Decision-makers</b>: Those who created or understand the
              tactics.
            </li>
            <li>
              <b>Squad Leaders</b>: Responsible for relaying and executing
              strategies.
            </li>
            <li>
              <b>Other Team Members</b>: Speak only when you have critical
              information that has not been covered.
            </li>
          </ol>
          <p>
            <b>Note</b>: This hierarchy can change if you have vital
            information, such as tent positions or urgent threats.
          </p>

          <h3>Communication in the Voice Channel</h3>
          <p>
            <b>Main Voice Channel</b>:
          </p>
          <ul>
            <li>
              <b>Squad Leaders</b> are primarily responsible for discussing
              strategy and making real-time adjustments.
            </li>
            <li>
              Others may contribute, but only with short, necessary updates
              (e.g., big enemy movements, tent/outpost locations, requests for
              help).
            </li>
          </ul>

          <p>
            <b>In-Game Squad Voice</b>:
          </p>
          <ul>
            <li>More casual communication is acceptable here.</li>
            <li>Focus on squad-specific objectives and tactics.</li>
          </ul>

          <h3>Using the Compass for Callouts</h3>
          <p>
            When making a call to a teammate who isn’t in your immediate
            vicinity:
          </p>
          <ul>
            <li>
              <b>Use the Compass</b>: Base your directions on your current
              position if your teammate is nearby. If distant, use a landmark or
              a cap spot as a reference.
            </li>
            <li>
              <b>Callout Structure</b>:{' '}
              <b>Teammate’s Name + What You Want to Call + Location</b>.
              Example: <i>“SKiLLiUS to Leftex: 1 Enemy northwest of B!”</i>
            </li>
            <li>
              <b>Task Requests</b>: Example:{' '}
              <i>
                “Exodus to Xispas: Spawn in base and take an outpost south of
                B.”
              </i>
            </li>
          </ul>

          <h2>BONUS TIPS</h2>
          <i>
            And here are some tips if you want to squeeze even more efficiency
            during clan wars for especially sweaty opponents.
          </i>
          <ul>
            <li>
              You can use <b>Vodka</b> builds as vodka effects don’t wear off
              after death. Vodka gives -1.5 and your body can tolerate 0.5
              extra, so you can use builds up to +2 rad. Make sure to have some
              vodka in personal storage, and every 10 minutes spawn at base to
              drink more.
            </li>{' '}
            <Suspense fallback={<div>Loading image...</div>}>
              <img
                src={vodka}
                alt='Clan War Strategy Visualization'
                className={styles.image}
              />
            </Suspense>
            <li>
              You can use <b>Brine</b> to get -1.5 bio. It works the same way as
              vodka but against bio infection. The best thing is that Vodka and
              Brine stack, and you can make <b>ridiculous builds</b> like 3x
              Viburnum branches and 3x Shards without counter artis.
            </li>
            <Suspense fallback={<div>Loading image...</div>}>
              <img
                src={brine}
                alt='Clan War Strategy Visualization'
                className={styles.image}
              />
            </Suspense>
            <li>
              You can gather or buy at auction some <b>Lemongrass</b>. It is a
              better morphine with more vitality, br, no poison and effects
              don’t wear off after death.
            </li>
            <Suspense fallback={<div>Loading image...</div>}>
              <img
                src={lemongrass}
                alt='Clan War Strategy Visualization'
                className={styles.image}
              />
            </Suspense>
            <li>
              You can ask <b>clan crafters</b> to craft you specific stuff you
              need for clan wars. Specific ammo, consumables, grenades or
              anything else.
            </li>
            <li>
              In the beginning, it is better to buy an extra <b>Desperol</b> to
              reach the position faster and then switch to morphine once you
              arrive.
            </li>
            <li>
              If you have money and time you can create a{' '}
              <b>second container</b> <i>(Or backpack with speed artis)</i>. 1
              container for speed 1 container for vitality. Use the second
              container at the start to reach points even faster and then switch
              to vitality..
            </li>{' '}
            <li>
              Sometimes it is better not to rush points and let enemies capture
              them, for example, in city bases. Enemies will be easy targets on
              points as there is not much cover and you will have high ground.
              You can kill them and retake points.
            </li>
          </ul>
        </div>
      </div>
    </LayoutGuide>
  );
}
